
.wrapperInput {
  margin: 20px 0 50px 40%;
}
.wrapperInput button{
  margin-left: 20px;
}
.detailsWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-width:600px; 
  min-height:500px; 
}
.detailsWrapper div{
  background:#f1f1f1; 
  text-align:center;
  padding: 10px;
}
.details {
  margin-left: 5%;
  min-width:600px; 
  min-height:600px; 
}
.selected {
  background-color: gray;
}
.list {
  min-width:400px; 
  min-height:600px; 
}
.list li {
  cursor: pointer;
  text-align: left;
}
.button {
  position: absolute;
  top: 21px;
  left: 800px;
}